      p –§–ª–æ—Ä–∏–Ω–¥–∞ –ø–æ–¥–Ω—è–ª–∞—Å—å. –ö–∞–∫ –æ–±—ã—á–Ω–æ, –º—ã —Å–∏–¥–µ–ª–∏ –≤ –µ–µ –≥–æ—Å—Ç–∏–Ω–æ–π. –Ø —Å–¥–µ–ª–∞–ª –≤—ã–≤–æ–¥, —á—Ç–æ –Ω–∞—à —Ä–∞–∑–≥–æ–≤–æ—Ä –æ–∫–æ–Ω—á–µ–Ω. –û–Ω–∞ —Å–∫–∞–∑–∞–ª–∞, —á—Ç–æ —É –Ω–µ–µ –µ—Å—Ç—å –µ—â–µ –æ–¥–Ω–∞ —Ç–µ–º–∞, —Å –∫–æ—Ç–æ—Ä–æ–π –æ–Ω–∞ —Ö–æ—á–µ—Ç –º–µ–Ω—è –ø–æ–∑–Ω–∞–∫–æ–º–∏—Ç—å, –ø—Ä–µ–∂–¥–µ —á–µ–º –ø–æ–ø—Ä–æ—â–∞—Ç—å—Å—è, –∏ –ø—Ä–∏–≤–µ–ª–∞ –≤ –¥—Ä—É–≥–æ–µ –ø–∞—Ç–∏–æ. –ù–∏–∫–æ–≥–¥–∞ —Ä–∞–Ω—å—à–µ —è –Ω–µ –±—ã–≤–∞–ª –≤ —ç—Ç–æ–π —á–∞—Å—Ç–∏ –µ–µ –¥–æ–º–∞. –û–Ω–∞ –º—è–≥–∫–æ –ø–æ–∑–≤–∞–ª–∞ –∫–æ–≥–æ-—Ç–æ, –∏ –∏–∑ –∫–æ–º–Ω–∞—Ç—ã –≤—ã—à–ª–∞ –∂–µ–Ω—â–∏–Ω–∞. –°–Ω–∞—á–∞–ª–∞ —è –Ω–µ —É–∑–Ω–∞–ª –µ–µ. –ñ–µ–Ω—â–∏–Ω–∞ –æ–±—Ä–∞—Ç–∏–ª–∞—Å—å –∫–æ –º–Ω–µ –ø–æ –∏–º–µ–Ω–∏, –∏ —Ç–æ–ª—å–∫–æ —Ç—É—Ç —è —Å–æ–æ–±—Ä–∞–∑–∏–ª, —á—Ç–æ —ç—Ç–æ –¥–æ–Ω—å—è –°–æ–ª–µ–¥–∞–¥, –Ω–æ —Å–æ–≤–µ—Ä—à–µ–Ω–Ω–æ –∏–∑–º–µ–Ω–∏–≤—à–∞—è—Å—è. –û–Ω–∞ —Å—Ç–∞–ª–∞ –Ω–∞–º–Ω–æ–≥–æ –º–æ–ª–æ–∂–µ –∏ —Å–∏–ª—å–Ω–µ–µ.
