<template>
  <div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å–ø–∏—Å–∫–∞–º–∏</p>
        <button class="delete" aria-label="close" @click="$parent.close()"></button>
      </header>

      <section class="modal-card-body">
        <div class="level lists-element is-mobile" v-for="(list, index) in listLists" :key="index">

          <div class="level-left">
            <div class="level-item">
              <b-radio v-model="selected" :native-value="list.id">{{ list.name }}</b-radio>
            </div>
          </div>

          <div class="level-right">
            <div class="level-item">
              <a class="button is-small is-dark" title="–ü–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞—Ç—å" @click="renameDialog(list)">
                <b-icon custom-size="mdi-18px" icon="pencil"></b-icon>
              </a>
            </div>
            <div class="level-item">
              <a class="button is-small is-dark" title="–≠–∫—Å–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å" @click="exportFile(list)">
                <b-icon custom-size="mdi-18px" icon="export"></b-icon>
              </a>
            </div>
            <div class="level-item">
              <a class="button is-small is-danger" title="–£–¥–∞–ª–∏—Ç—å" @click="deleteDialog(list)">
                <b-icon custom-size="mdi-18px" icon="delete"></b-icon>
              </a>
            </div>
          </div>

        </div>
      </section>

      <footer class="modal-card-foot">
        <a class="button is-dark" @click="createList()">
          <b-icon icon="plus"></b-icon>
          <span>–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—ã–π</span>
        </a>

        <b-upload accept=".json" v-model="file" @input="importFile()">
          <a class="button is-dark">
            <b-icon icon="upload"></b-icon>
            <span>–ò–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å</span>
          </a>
        </b-upload>
      </footer>

    </div>
  </div>
</template>

<script>
  import { mapState, mapActions } from "vuex"

  export default {
    data() {
      return {
        file: [],
      }
    },

    computed: {
      ...mapState({
        listLists: state => state.list.listLists
      }),

      selected: {
        get() {
          return this.$store.state.list.openListId;
        },
        set(value) {
          this.changeList(value)
        }
      }
    },

    methods: {
      ...mapActions(["createList", "changeList"]),

      renameDialog(list) {
        this.$dialog.prompt({
          message: "–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ:",
          confirmText: "–ì–æ—Ç–æ–≤–æ",
          cancelText: "–û—Ç–º–µ–Ω–∞",
          inputAttrs: {
            value: list.name,
            maxlength: 30
          },
          onConfirm: (value) => this.$store.dispatch("renameList", { id: list.id, name: value })
        })
      },

      deleteDialog(list) {
        this.$dialog.confirm({
          title: "–£–¥–∞–ª–µ–Ω–∏–µ",
          message: "–í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —Å–ø–∏—Å–æ–∫? –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–ª—å–∑—è –æ—Ç–º–µ–Ω–∏—Ç—å.",
          confirmText: "–£–¥–∞–ª–∏—Ç—å",
          cancelText: "–û—Ç–º–µ–Ω–∞",
          type: "is-danger",
          hasIcon: true,
          onConfirm: () => this.$store.dispatch("deleteList", list.id)
        })
      },

      exportFile(list) {
        this.$store.dispatch("exportList", list.id)
      },

      importFile() {
        this.$store.dispatch("importList", this.file[0])
      },

    }
  }
</script>


