<template>
  <div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">–°–ø—Ä–∞–≤–∫–∞ –ø–æ –ø–æ–∏—Å–∫—É</p>
        <button class="delete" aria-label="close" @click="$parent.close()"></button>
      </header>

      <section class="modal-card-body">
        <div class="content">
          <ul>
            <li>–í –ø–∞—Ä–∞–º–µ—Ç—Ä–∞—Ö –º–æ–∂–Ω–æ –≤—ã–±—Ä–∞—Ç—å —Å–ø–∏—Å–æ–∫ –∫–Ω–∏–≥, –ø–æ –∫–æ—Ç–æ—Ä—ã–º –±—É–¥–µ—Ç –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç—å—Å—è –ø–æ–∏—Å–∫.</li>
            <li>–î–ª—è –Ω–∞—Ö–æ–∂–¥–µ–Ω–∏—è —Ç–æ—á–Ω–æ–π —Ñ—Ä–∞–∑—ã –∏—Å–ø–æ–ª—å–∑—É–π—Ç–µ –∫–∞–≤—ã—á–∫–∏, –Ω–∞–ø—Ä–∏–º–µ—Ä, <code>"–º—è—Å–æ —Å–∏–ª—ã"</code>.</li>
            <li>–ó–Ω–∞–∫ <code>-</code> —É–∫–∞–∑–∞–Ω–Ω—ã–π –ø–µ—Ä–µ–¥ —Å–ª–æ–≤–æ–º –∏–ª–∏ —Ñ—Ä–∞–∑–æ–π –∏—Å–∫–ª—é—á–∞–µ—Ç –∏—Ö –∏–∑ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä, <code>–º—è—Å–æ -—Å–∏–ª—ã</code>.</li>
            <li>–ê –≤—Å—ë –æ—Ç–º–µ—á–µ–Ω–Ω–æ–µ –∑–Ω–∞–∫–æ–º <code>+</code> –Ω–∞–æ–±–æ—Ä–æ—Ç –±—É–¥–µ—Ç –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å—Å—è –≤ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞—Ö –ø–æ–∏—Å–∫–∞.</li>
            <li>–î–ª—è –ø–æ–∏—Å–∫–∞ –ø–æ –º–∞—Å–∫–µ –º–æ–≥—É—Ç –±—ã—Ç—å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω—ã –ø–æ–¥—Å—Ç–∞–Ω–æ–≤–æ—á–Ω—ã–µ –∑–Ω–∞–∫–∏ <code>?</code> (0 –∏–ª–∏ 1 —Å–∏–º–≤–æ–ª) –∏ <code>*</code> (0 –∏–ª–∏ –±–æ–ª–µ–µ —Å–∏–º–≤–æ–ª–æ–≤). –ù–∞–ø—Ä–∏–º–µ—Ä, <code>—Å–Ω–æ–≤–∏–¥*</code>.</li>
            <li>–°–ª–æ–≤–∞ –∏ —Ñ—Ä–∞–∑—ã –º–æ–≥—É—Ç –±—ã—Ç—å —Å–∫–æ–º–±–∏–Ω–∏—Ä–æ–≤–∞–Ω—ã —Å –ø–æ–º–æ—â—å—é –ª–æ–≥–∏—á–µ—Å–∫–∏—Ö –æ–ø–µ—Ä–∞—Ç–æ—Ä–æ–≤ <code>AND</code> –∏ <code>OR</code>. –ù–∞–ø—Ä–∏–º–µ—Ä, <code>—Å–Ω–æ–≤–∏–¥* AND —Ä—É–∫–∏</code>.</li>
          </ul>
          <p>–ë–æ–ª–µ–µ –ø–æ–¥—Ä–æ–±–Ω—É—é –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ —Å–∏–Ω—Ç–∞–∫—Å–∏—Å–µ –ø–æ–∏—Å–∫–æ–≤—ã—Ö –∑–∞–ø—Ä–æ—Å–æ–≤ –º–æ–∂–Ω–æ –Ω–∞–π—Ç–∏ –≤ —Å–ø—Ä–∞–≤–æ—á–Ω—ã—Ö –º–∞—Ç–µ—Ä–∏–∞–ª–∞—Ö
          <a href="https://www.elastic.co/guide/en/elasticsearch/reference/current/query-dsl-query-string-query.html#query-string-syntax" target="_blank">Elasticsearch</a>.
          </p>
        </div>
      </section>

      <footer class="modal-card-foot"></footer>

    </div>
  </div>
</template>

<script>
  import { mapState, mapActions } from "vuex"

  export default {
    data() {
      return {
        file: [],
      }
    },

    computed: {
      ...mapState({
        listLists: state => state.list.listLists
      }),

      selected: {
        get() {
          return this.$store.state.list.openListId;
        },
        set(value) {
          this.changeList(value)
        }
      }
    },

    methods: {
      ...mapActions(["createList", "changeList"]),

      renameDialog(list) {
        this.$dialog.prompt({
          message: "–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ:",
          confirmText: "–ì–æ—Ç–æ–≤–æ",
          cancelText: "–û—Ç–º–µ–Ω–∞",
          inputAttrs: {
            value: list.name,
            maxlength: 30
          },
          onConfirm: (value) => this.$store.dispatch("renameList", { id: list.id, name: value })
        })
      },

      deleteDialog(list) {
        this.$dialog.confirm({
          title: "–£–¥–∞–ª–µ–Ω–∏–µ",
          message: "–í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å —Å–ø–∏—Å–æ–∫? –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–ª—å–∑—è –æ—Ç–º–µ–Ω–∏—Ç—å.",
          confirmText: "–£–¥–∞–ª–∏—Ç—å",
          cancelText: "–û—Ç–º–µ–Ω–∞",
          type: "is-danger",
          hasIcon: true,
          onConfirm: () => this.$store.dispatch("deleteList", list.id)
        })
      },

      exportFile(list) {
        this.$store.dispatch("exportList", list.id)
      },

      importFile() {
        this.$store.dispatch("importList", this.file[0])
      },

    }
  }
</script>


