<template>
  <div v-shortkey.native="shortcuts" @shortkey="keyPress">
    <slot/>
  </div>
</template>

<script>
  import { mapState, mapMutations, mapActions } from "vuex"

  export default {
    name: "hotkeys",

    data() {
      return {
        shortcuts: {
          up: ["arrowup"],
          down: ["arrowdown"],
          left: ["arrowleft"],
          right: ["arrowright"],
          enter: ["enter"],
          rename: ["f2"],
          tab: ["tab"],
          delete: ["del"]
        }
      }
    },

    computed: {
      ...mapState({
        openList: state => state.list.openList,
        flatTree: state => state.list.flatTree,
        selectedItem: state => state.list.selectedItem,
        addMode: state => state.list.addMode,
        editMode: state => state.list.editMode,
      }),
    },

    methods: {
      ...mapMutations(["SET_SELECTED_ITEM", "TOGGLE_ADD_MODE", "TOGGLE_EDIT_MODE", "DELETE_ITEM", "TOGGLE_EXPAND", "SET_EXPAND"]),
      ...mapActions(["initList", "addDone", "addCancel", "editItem", "editDone", "editCancel", "selectUp", "selectDown", "removeItem"]),

      keyPress: function(event) {
        switch (event.srcKey) {
          case "up": // –í—ã–¥–µ–ª–µ–Ω–∏–µ –≤–≤–µ—Ä—Ö
            {
              this.selectUp()
              break
            }
          case "down": // –í—ã–¥–µ–ª–µ–Ω–∏–µ –≤–Ω–∏–∑
            {
              this.selectDown()
              break
            }
          case "left": // –õ–µ–≤–∞—è —Å—Ç—Ä–µ–ª–∫–∞ —Å–≤–æ—Ä–∞—á–∏–≤–∞–µ—Ç –≤–µ—Ç–≤—å
            {
              if (!this.addMode && !this.editMode) {
                this.SET_EXPAND(false)
              }
              break
            }
          case "right": // –ü—Ä–∞–≤–∞—è —Å—Ç—Ä–µ–ª–∫–∞ —Ä–∞–∑–≤–æ—Ä–∞—á–∏–≤–∞–µ—Ç
            {
              if (!this.addMode && !this.editMode) {
                this.SET_EXPAND(true)
              }
              break
            }
          case "enter": // Enter –∑–∞–ø—É—Å–∫–∞–µ—Ç –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
            {
              //if (!this.addMode)
              //   this.addDone(this.selectedItem)
              // } else if (!this.editMode) {
              this.TOGGLE_ADD_MODE(true)
              // } else {
              //   this.editDone(this.selectedItem)
              // }
              break
            }
          case "rename": // F2 –∑–∞–ø—É—Å–∫–∞–µ—Ç –∏–ª–∏ –æ—Ç–º–µ–Ω—è–µ—Ç –ø–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ
            {
              if (!this.editMode) {
                this.editItem(this.selectedItem)
              } else {
                this.TOGGLE_EDIT_MODE(false)
              }
              break
            }
          case "delete": // –£–¥–∞–ª–µ–Ω–∏–µ –≤—ã–¥–µ–ª–µ–Ω–Ω–æ–≥–æ
            {
              this.removeItem()
              this.selectDown()
              break
            }
          case "tab":
            {
              this.selectDown()
              break
            }
        }
      }
    },

  }
</script>


