import moment from "moment";
moment.locale("ru");

// –°–∫–ª–æ–Ω–µ–Ω–∏—è –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤ –≤ –º–∞—Å—Å–∏–≤–µ titles –º–æ–∂–Ω–æ –æ–ø—Ä–µ–¥–µ–ª–∏—Ç—å, –ø—Ä–∏—Å—Ç–∞–≤–ª—è—è —Å–ª–æ–≤–æ –∫ —Ü–∏—Ñ—Ä–∞–º 1, 3 –∏ 5
function declint(number, titles) {
  let cases = [2, 0, 1, 1, 1, 2];
  return number + ' ' + titles[ (number%100>4 && number%100<20)? 2 : cases[(number%10<5)?number%10:5] ];
}

export default {

   searchInfo: state => {
    if (state.searchStats) {
      let hits =  declint(state.searchStats.total, ['—Ä–µ–∑—É–ª—å—Ç–∞—Ç', '—Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞', '—Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤'])
      return hits + ' (' + state.searchStats.took + ' –º—Å)'
    }
  },

  // –ü–æ–¥—Å—á—ë—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ —Å–ø–∏—Å–∫–µ
  itemsInList: state => {
    let items = 0

    function count(list) {
      for (let item of list) {
        items++
        count(item.children)
      }
      return items
    }
    count(state.openList.tree)
    return declint(items, ['–∑–∞–ø–∏—Å—å', '–∑–∞–ø–∏—Å–∏', '–∑–∞–ø–∏—Å–µ–π'])
  },

  createdDate: state => {
    let date = moment(state.openList.created).format("DD.MM.YY");
    let ago = moment(state.openList.created).fromNow()
    return `${date} (${ago})`
  }

}



