<template>
  <div id="list">
    <hotkeys>
      <div class="columns is-centered">
        <div class="column is-9-tablet is-8-desktop is-7-widescreen is-6-fullhd">

          <b-notification class="notify" :active.sync="isShowStartNotify" @close="$store.commit('SET_START_NOTIFY', false)">
            <p>–ù–∞ –¥–∞–Ω–Ω—ã–π –º–æ–º–µ–Ω—Ç –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ —Ö–æ—Ç—å –∏ –±–æ–ª–µ–µ-–º–µ–Ω–µ–µ —Ä–∞–±–æ—Ç–∞–µ—Ç, –Ω–æ –æ–Ω–æ –≤—Å—ë –µ—â—ë –æ—á–µ–Ω—å —Å—ã—Ä–æ–µ, –ø–æ—ç—Ç–æ–º—É –Ω–µ —Ä–µ–∫–æ–º–µ–Ω–¥—É–µ—Ç—Å—è –µ–≥–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –¥–ª—è —á–µ–≥–æ-—Ç–æ –∫—Ä–æ–º–µ —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è.</p>
            <!-- <p>–û —Ç–µ—Ö–Ω–∏–∫–µ –ø–µ—Ä–µ–ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∏ –æ —Ç–æ–º, –∫–∞–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è —ç—Ç–∏–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ–º –º–æ–∂–Ω–æ –ø—Ä–æ—á–∏—Ç–∞—Ç—å –≤ <router-link to="/list-info">—Ä–∞–∑–¥–µ–ª–µ —Å–ø—Ä–∞–≤–∫–∏</router-link>.</p> -->
          </b-notification>

          <nav class="level tree-header is-mobile">
            <div class="level-left">
              <div class="level-item">
                <a class="invert-link" @click="isListsMenu = !isListsMenu">
                  <span>{{ openList.name }}</span>
                </a>
              </div>
            </div>
            <div class="level-right">
              <div class="level-item">
                <b-dropdown>
                  <a class="invert-link" slot="trigger">
                    <span>{{ itemsInList }}</span>
                  </a>
                  <b-dropdown-item custom>
                    <div class="list-header-info">
                      <span>–°–æ–∑–¥–∞–Ω:</span>
                      <span>{{ createdDate }}</span>
                    </div>
                  </b-dropdown-item>
                </b-dropdown>
              </div>

              <div class="level-item">
                <router-link class="icon icon-link is-borderless" to="/list-info">
                  <b-icon icon="help-circle-outline"></b-icon>
                </router-link>
              </div>

            </div>
          </nav>

          <b-modal :active.sync="isListsMenu" :canCancel="['escape', 'outside']" has-modal-card>
            <lists-modal />
          </b-modal>

          <tree class="tree" :treeData="openList.tree" />
          <debug-panel @clear="—ÅlearStorage()" v-show="isDebug" />

        </div>
      </div>
    </hotkeys>
  </div>
</template>

<script>
  import Tree from "@/components/list/Tree";
  import Hotkeys from "@/components/list/Hotkeys";
  import DebugPanel from "@/components/list/DebugPanel";
  import ListsModal from "@/components/list/ListsModal";
  import { mapState, mapActions, mapGetters } from "vuex"

  export default {
    name: "MainPage",

    components: {
      Tree,
      Hotkeys,
      DebugPanel,
      ListsModal
    },

    data() {
      return {
        isListsMenu: false,
        isSearch: false,
      }
    },

    computed: {
      ...mapState({
        openList: state => state.list.openList,
        isDebug: state => state.list.isDebug,
        isShowStartNotify: state => state.list.isShowStartNotify,
        isShowDeleteSnack: state => state.list.isShowDeleteSnack,
      }),
      ...mapGetters(["itemsInList", "createdDate"])
    },

    created() {
      this.$store.dispatch("initList")
      document.title = "–°–ø–∏—Å–æ–∫ –ø–µ—Ä–µ–ø—Ä–æ—Å–º–æ—Ç—Ä–∞"
    },

    watch: {
      isShowDeleteSnack: {
        handler: function() {
          this.deleteSnackbar()
        }
      }
    },

    methods: {
      deleteSnackbar() {
        this.$snackbar.open({
          duration: 20000,
          message: '–≠–ª–µ–º–µ–Ω—Ç —É–¥–∞–ª—ë–Ω',
          type: 'is-danger',
          position: 'is-bottom-right',
          actionText: '–û–¢–ú–ï–ù–ê',
          queue: false,
          onAction: () => {
            console.log("ON")
          }
        })
      }
    },

    directives: {
      focus: {
        inserted: function(el) {
          el.focus()
        }
      }
    }
  };
</script>

<style>
</style>


